import { MoreHoriz } from '@material-ui/icons'
import "./post.css"
import { Users } from "../../dummyData"


export default function post({post}) {
  return (
    <div className='post'>
        <div className='postWrapper'>
            <div className='postTop'>
                <img src={Users.filter((u)=> u.id===post.userId)[0].profilePicture}/>
                <div className='postProfileName'>
                    <span>{Users.filter((u)=>u.id===post.userId)[0].username}</span>
                    <p>{post.date}</p>
                </div>
                <MoreHoriz className='threeDot'/>
            </div>
            <div className='postImg'>
                <div className='postQuotes'>{post?.desc}</div>
                <div className='postImage'><img src={post.photo}/></div>
            </div>
            <div className='postBottom'>
                <div className='postLikeCounter'>
                    <img src='/assets/like.png'/>
                    <img src='/assets/heart.png'/>
                    <span><strong>Anuj Sharma</strong>, <strong>Vishal Sharma</strong> and {post.like} Others Like this</span>
                    <p>{post.comment} Comments</p>
                </div>
            </div>
        </div>
    </div>
  )
}
