import "./topbar.css";
import { Chat, Person, Search, Notifications } from "@material-ui/icons";

export default function Topbar() {
  return (
    <div className="topbarContainer">
      <div className="topbarLeft">
        <div className="logo">AmanSocial</div>
      </div>
      <div className="topbarCenter">
        <div className="searchBar">
          <Search />
          <input placeholder="Search for Friends or Places" />
        </div>
      </div>
      <div className="topbarRight">
        <div className="menuBtn">
          <div className="Home">Home</div>
          <div className="Timeline">Timeline</div>
        </div>
        <div className="notificationBtn">
          <div className="friendReq notificationIcon">
            <Person />
            <span className="notificationBadge">1</span>
          </div>
          <div className="chatIcon notificationIcon">
            <Chat />
            <span className="notificationBadge">2</span>
          </div>
          <div className="notification notificationIcon">
            <Notifications />
            <span className="notificationBadge">1</span>
          </div>
        </div>
      </div>

      <div className="profile">
        <img src="/assets/person/1.jpeg"/>
      </div>
    </div>
  );
}
