import { Bookmark, Chat, Group, HelpOutline, PlayCircleFilledWhite, Event, RssFeed, School, WorkOutline } from "@material-ui/icons";
import SidebarFriend from "../SidebarFriend/SidebarFriend";
import "./sidebar.css";
import {Users} from "../../dummyData"

export default function Sidebar() {
  return (
    <div className="sidebar">
        <div className="sidebarWrapper">
            <div className="sidebarIcon">
                <div className="feed1 sidebarAlign">
                    <RssFeed/>
                    <span>Feed</span>
                </div>
                <div className="chat sidebarAlign">
                    <Chat/>
                    <span>Chat</span>
                </div>
                <div className="videos sidebarAlign">
                    <PlayCircleFilledWhite/>
                    <span>Videos</span>
                </div>
                <div className="groups sidebarAlign">
                    <Group/>
                    <span>Groups</span>
                </div>
                <div className="bookmarks sidebarAlign">
                    <Bookmark/>
                    <span>Bookmarks</span>
                </div>
                <div className="questions sidebarAlign">
                    <HelpOutline/>
                    <span>Questions</span>
                </div>
                <div className="jobs sidebarAlign">
                    <WorkOutline/>
                    <span>Jobs</span>
                </div>
                <div className="events sidebarAlign">
                    <Event/>
                    <span>Events</span>
                </div>
                <div className="courses sidebarAlign">
                    <School/>
                    <span>Courses</span>
                </div>
            </div>
            <div className="sidebarBtn">
                <button>Show More</button>
            </div>
            <hr/>

            <div className="sidebarFriends">
                {Users.map((u)=>(
                    <SidebarFriend key={u.id} user={u}/>
                ))}
            </div>
        </div>
    </div>
  )
}
