import { AddAPhoto, EmojiEmotions, Label, LocationOn } from "@material-ui/icons";
import "./share.css";

export default function Share() {
  return (
    <div className="share">
        <div className="shareWrpper">
            <div className="shareTop">
                <img src="/assets/person/1.jpeg" />
                <input type="text" placeholder="What's in your mind Aman?"/>
            </div>
            <hr className="shareHr"/>
            <div className="shareBottom">
                <div className="shareIcon">
                    <div className="shareIconBtn">
                        <AddAPhoto className="addaphoto"/>
                        <span>Photo or Video</span>
                    </div>
                    <div className="shareIconBtn">
                        <Label className="label"/>
                        <span>Tag</span>
                    </div>
                    <div className="shareIconBtn">
                        <LocationOn className="location"/>
                        <span>Location</span>
                    </div>
                    <div className="shareIconBtn">
                        <EmojiEmotions className="emojiemotions"/>
                        <span>Feelings</span>
                    </div>
                </div>
                <div shareBtn>
                    <button className="shareButton">Share</button>
                </div>
            </div>
        </div>
    </div>
  )
}
